.img {
	max-width: 100%;
	display: inline;
}

.title {
	margin-bottom: 15px;
	font-size: 1.5rem;
}

.points {
	display: inline;
	max-width: 40%;
}

.points li {
	margin-left: 20px;
	font-size: 1.1rem;
	margin-top: 4px;
}

ul.points span:first-child {
	font-weight: bold;
	margin-right: 15px;
}

.unit {
	font-style: italic;
	font-size: 0.95rem;
	margin-left: 1px;
}

li.uploaded {
	font-size: 0.9rem;
}

li.desc {
	margin-bottom: 20px;
}

/* error alert section */
/* The alert message box */
.alert {
    padding: 20px;
    background-color: #f44336; /* Red */
    color: white;
    margin-bottom: 15px;
}

/* The close button */
.closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

/* When moving the mouse over the close button */
.closebtn:hover {
    color: black;
}


